// components/AuthMenu.tsx
'use client'

import { useUser, SignInButton, SignUpButton, SignOutButton } from '@clerk/nextjs'
import {
  DropdownMenu,
  DropdownMenuTrigger,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuSeparator
} from '@/components/ui/dropdown-menu'
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar'

export function AuthMenu() {
  const { user, isLoaded, isSignedIn } = useUser()

  // Wait for Clerk to finish loading user state
  if (!isLoaded) {
    return null
  }

  return (
    <DropdownMenu>
      <DropdownMenuTrigger asChild>
        {isSignedIn && user ? (
          <Avatar className="cursor-pointer">
            <AvatarImage src={user.setProfileImage} alt={user.fullName || 'User'} />
            <AvatarFallback>{(user.firstName || 'U')[0]}</AvatarFallback>
          </Avatar>
        ) : (
          <Avatar className="cursor-pointer">
            <AvatarFallback>?</AvatarFallback>
          </Avatar>
        )}
      </DropdownMenuTrigger>

      <DropdownMenuContent align="end" sideOffset={4}>
        {isSignedIn && user ? (
          <>
            <DropdownMenuItem disabled className="font-medium">
              {user.fullName || user.primaryEmailAddress?.emailAddress || 'User'}
            </DropdownMenuItem>
            <DropdownMenuSeparator />
            <SignOutButton>
              <DropdownMenuItem asChild>
                <button className="w-full text-left">Sign out</button>
              </DropdownMenuItem>
            </SignOutButton>
          </>
        ) : (
          <>
            <SignInButton>
              <DropdownMenuItem asChild>
                <button className="w-full text-left">Sign in</button>
              </DropdownMenuItem>
            </SignInButton>
            <SignUpButton>
              <DropdownMenuItem asChild>
                <button className="w-full text-left">Register</button>
              </DropdownMenuItem>
            </SignUpButton>
          </>
        )}
      </DropdownMenuContent>
    </DropdownMenu>
  )
}
